import React, { useState, useEffect, useRef, useMemo,useContext } from "react";
import axios from "axios";
import { Context } from "../HrmsContext";

export default function TicketModal({
  ticket,
  onClose,
  replyText,
  setReplyText,
  roles,
  onStatusUpdate,
}) {
  const [showChat, setShowChat] = useState(false);
  const [replies, setReplies] = useState([]);
  const [loading, setLoading] = useState(false);
  const [messageSent, setMessageSent] = useState(false);
  const messagesEndRef = useRef(null);
  const socketRef = useRef(null);
  const [newStatus, setNewStatus] = useState(ticket?.status || "");

  const [page, setPage] = useState(0);
  const [hasMore, setHasMore] = useState(true);
  const chatContainerRef = useRef(null);

const prevScrollHeight = useRef(0);
const [loadingOlder, setLoadingOlder] = useState(false);
 const { theme } = useContext(Context);
  const isDark = theme === "dark";


  
  const formatDate = (date) => {
    const d = new Date(date);
    const today = new Date();
    const yesterday = new Date();
    yesterday.setDate(today.getDate() - 1);

    if (d.toDateString() === today.toDateString()) return "Today";
    if (d.toDateString() === yesterday.toDateString()) return "Yesterday";
    return d.toLocaleDateString([], {
      day: "2-digit",
      month: "short",
      year: "numeric",
    });
  };

  
  const formatToIST = (dateString) => {
    if (!dateString) return "";
    const date = new Date(dateString.endsWith("Z") ? dateString : dateString + "Z");
    return date.toLocaleString("en-IN", {
      timeZone: "Asia/Kolkata",
      hour: "2-digit",
      minute: "2-digit",
      day: "2-digit",
      month: "short",
      year: "numeric",
      hour12: true,
    });
  };

  
  const formatChatTimeIST = (dateString) => {
    if (!dateString) return "";
    const date = new Date(dateString.endsWith("Z") ? dateString : dateString + "Z");
    return date.toLocaleTimeString("en-IN", {
      timeZone: "Asia/Kolkata",
      hour: "2-digit",
      minute: "2-digit",
      hour12: true,
    });
  };

 
  const groupedReplies = useMemo(() => {
    const groups = {};
    const sortedReplies = [...replies].sort(
      (a, b) => new Date(a.repliedAt) - new Date(b.repliedAt)
    );
    sortedReplies.forEach((msg) => {
      const dateLabel = formatDate(msg.repliedAt);
      if (!groups[dateLabel]) groups[dateLabel] = [];
      groups[dateLabel].push(msg);
    });
    return groups;
  }, [replies]);

  
  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  }, [replies]);

 
 useEffect(() => {
  if (!ticket?.ticketId) return;

  if (socketRef.current) {
    socketRef.current.close();
  }
  const token = localStorage.getItem("accessToken");

  const ws = new WebSocket(
    `wss://hrms.anasolconsultancyservices.com/api/ticket/ws?ticketId=${ticket.ticketId}?token=${token}`
  );
  socketRef.current = ws;

  ws.onopen = () => console.log("‚úÖ WebSocket connected for", ticket.ticketId);

  ws.onmessage = (event) => {
    try {
      const msg = JSON.parse(event.data);
      console.log("üì© Incoming WS message:", msg);

      if (msg && msg.replyText) {
        setReplies((prev) => {
          const exists = prev.some(
            (m) =>
              (m.id && msg.id && m.id === msg.id) ||
              (m.replyText === msg.replyText && m.repliedAt === msg.repliedAt)
          );
          return exists ? prev : [...prev, msg];
        });
      }
    } catch (e) {
      console.error("‚ùå Error parsing WebSocket message", e, event.data);
    }
  };

  ws.onerror = (err) => console.error("‚ùå WebSocket error:", err);
  ws.onclose = () => console.log("üîå WebSocket disconnected");

  return () => ws.close();
}, [ticket?.ticketId]);


const fetchReplies = async (pageNum = 0) => {
  if (!ticket?.ticketId || loading || !hasMore) return;
  setLoading(true);

  try {
    const container = chatContainerRef.current;
    if (pageNum > 0 && container) {
      prevScrollHeight.current = container.scrollHeight;
      setLoadingOlder(true);
    }

    const token = localStorage.getItem("accessToken");
    const response = await axios.get(
      `https://hrms.anasolconsultancyservices.com/api/ticket/admin/tickets/${ticket.ticketId}/reply`,
      {
        headers: { Authorization: `Bearer ${token}` },
        params: { page: pageNum, size: 20 },
      }
    );

    const messagesArray = Array.isArray(response.data?.content)
      ? response.data.content
      : [];

    if (messagesArray.length === 0) {
      setHasMore(false);
    } else {
      if (pageNum === 0) {
        setReplies(messagesArray);
      } else {
        setReplies((prev) => [...messagesArray, ...prev]); 
      }
      setPage(pageNum);
    }
  } catch (error) {
    console.error("Failed to fetch replies", error);
  } finally {
    setLoading(false);
  }
};


useEffect(() => {
  if (messageSent && messagesEndRef.current) {
    messagesEndRef.current.scrollIntoView({ behavior: "smooth" });
  }
}, [messageSent]);


useEffect(() => {
  if (loadingOlder && chatContainerRef.current) {
    const container = chatContainerRef.current;
    const newScrollHeight = container.scrollHeight;
    container.scrollTop = newScrollHeight - prevScrollHeight.current;
    setLoadingOlder(false);
  }
}, [replies]);




  useEffect(() => {
    if (showChat) fetchReplies(0);
  }, [ticket?.ticketId, showChat]);

useEffect(() => {
  const container = chatContainerRef.current;
  if (!container) return;

  const handleScroll = () => {
    if (container.scrollTop === 0 && hasMore && !loading) {
      fetchReplies(page + 1);
    }
  };

  container.addEventListener("scroll", handleScroll);
  return () => container.removeEventListener("scroll", handleScroll);
}, [page, hasMore, loading]);




const handleReply = async () => {
  if (!replyText.trim() || !ticket?.ticketId) return;

  const token = localStorage.getItem("accessToken");
  const payload = {
    replyText: replyText.trim(),
    repliedBy: "admin",
    status: newStatus,
    employeeId: ticket.employeeId || "DEFAULT_EMPLOYEE",
    roles: roles && typeof roles === "string" ? `ROLE_${roles.toUpperCase()}` : "ROLE_HR",
    repliedAt: new Date().toISOString(),
  };

  try {
    const res = await axios.put(
      `https://hrms.anasolconsultancyservices.com/api/ticket/admin/tickets/${ticket.ticketId}/reply`,
      payload,
      { headers: { Authorization: `Bearer ${token}` } }
    );

   
    setReplies((prev) => [...prev, res.data || payload]);

    setReplyText("");
    setMessageSent(true);
    setTimeout(() => setMessageSent(false), 2000);
  } catch (error) {
    console.error(" Reply failed", error.response?.data || error.message);
  }
};

 
 
  const handleStatusChange = async () => {
    if (!ticket?.ticketId) return;

    const token = localStorage.getItem("accessToken");
    const payload = {
      replyText: replyText.trim() || "Status updated",
      repliedBy: "admin",
      status: newStatus,
      employeeId: ticket.employeeId || "DEFAULT_EMPLOYEE",
      roles: roles && typeof roles === "string" ? `ROLE_${roles.toUpperCase()}` : "ROLE_HR",
      repliedAt: new Date().toISOString(),
    };

    try {
      const res = await axios.put(
        `https://hrms.anasolconsultancyservices.com/api/ticket/admin/tickets/${ticket.ticketId}/reply`,
        payload,
        { headers: { Authorization: `Bearer ${token}` } }
      );

      setReplies((prev) => [...prev, res.data]);
      setNewStatus(res.data.status);
      ticket.status = res.data.status;
      if (onStatusUpdate) onStatusUpdate(res.data);
      onClose();
    } catch (err) {
      console.error("Error updating status:", err.response?.data || err.message);
    }
  };

    const bgColor = isDark ? "bg-gray-900 text-white" : "bg-white text-gray-900";
  const chatBg = isDark ? "bg-gray-800 text-white" : "bg-white text-gray-900";
  const chatAltBg = isDark ? "bg-gray-700 text-white" : "bg-white text-gray-900";
 const borderColor =
  bgColor === isDark
    ? "border-white"
    : bgColor === !isDark 
    ? "border-black"
    : "border-gray-300"; 


  const displayRole = (role) => role?.replace(/^ROLE_/, "");

  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/40 backdrop-blur-sm p-4">
      <div className={`${bgColor} w-full max-w-[calc(100vw-5rem)] md:max-w-4xl rounded-2xl shadow-2xl relative transition-all duration-300`}>
        <div className={`p-6 overflow-y-auto h-[90vh] scroll-smooth ${bgColor}`}>
          {!showChat ? (
            <>
                <h2 className={`text-3xl font-extrabold mb-6 ${isDark ? "text-white" : "text-blue-700"}`}>
                üé´ Ticket Details
              </h2>

              <div className={`grid grid-cols-1 md:grid-cols-2 gap-5 text-[15px] mb-8 ${isDark ? "text-gray-200" : "text-gray-800"}`}>
                <p>
                  <span className="font-semibold">üë§ Employee ID:</span>{" "}
                  {ticket.employeeId}
                </p>
                <p>
                  <span className="font-semibold">üìõ Ticket ID:</span>{" "}
                  {ticket.ticketId}
                </p>
                <p>
                  <span className="font-semibold">üìù Issue:</span> {ticket.title}
                </p>
                <p>
                  <span className="font-semibold">üßë‚Äçüíº Role:</span>{" "}
                  {displayRole(ticket.roles)}
                </p>
                <p>
                  <span className="font-semibold">‚ö†Ô∏è Description:</span>{" "}
                  {ticket.description}
                </p>
                <p>
                  <span className="font-semibold">‚ö†Ô∏è Priority:</span>{" "}
                  {ticket.priority}
                </p>
               <p className="md:col-span-2">
  <span className="font-semibold">üìÖ Created:</span>{" "}
  {formatToIST(ticket.sentAt)}
</p>


                <div className="md:col-span-2">
                  <label className={`block font-medium text-gray-900 mb-2 ${isDark ? "text-white" : "text-blue-700"}`}>
                    üìå Update Status
                  </label>
                 <select
  value={newStatus}
  onChange={(e) => setNewStatus(e.target.value)}
  className={`w-full rounded-lg px-3 py-2 text-sm border focus:ring-2 focus:ring-blue-500 ${bgColor} ${borderColor}`}
>
  <option value="Unsolved">Unsolved</option>
  <option value="Resolved">Resolved</option>
</select>

                </div>
              </div>

               <button
                onClick={() => setShowChat(true)}
                className="mb-6 px-6 py-2 bg-blue-600 text-white font-medium rounded-full hover:bg-blue-700 transition-all duration-200 shadow"
              >
                üí¨ Open Chat
              </button>

              <div className="mt-10 flex justify-end gap-4">
                 <button
                  onClick={onClose}
                  className={`px-5 py-2 rounded-lg border ${borderColor} ${isDark ? "text-gray-200 hover:bg-gray-800" : "text-gray-600 hover:bg-gray-100"} transition`}
                >
                  Cancel
                </button>
                 <button
                  onClick={handleStatusChange}
                  disabled={loading}
                  className={`px-6 py-2 rounded-lg font-medium transition  ${borderColor} ${loading ? "bg-gray-400 text-white cursor-not-allowed" : "bg-blue-600 text-white hover:bg-blue-700"}`}
                >
                  {loading ? "Saving..." : "Update Status"}
                </button>
              </div>
            </>
          ) : (
            <>
              <div className="flex justify-between items-center mb-6">
                  <h2 className={`text-2xl font-bold ${isDark ? "text-white" : "text-blue-700"}`}>üí¨ Chat with {ticket.employeeId}</h2>
                <button onClick={() => setShowChat(false)} className={`text-sm ${isDark ? "text-blue-400 hover:text-blue-200" : "text-blue-600 hover:underline"}`}>
                  Back to Ticket
                </button>
              </div>

              <div ref={chatContainerRef} className={`max-h-72 overflow-y-auto space-y-3 p-4 rounded-xl border ${borderColor} shadow-inner ${isDark ? "bg-gray-800" : "bg-gray-50"}`}>
                <div className={`${chatBg} border rounded-bl-none px-4 py-2 max-w-[75%] text-sm rounded-2xl shadow`}>
                  <p>{ticket.title}</p>
                  <div className="text-[10px] mt-1 text-left text-gray-500">{formatChatTimeIST(ticket.sentAt)}</div>
                </div>

               {Object.keys(groupedReplies).map((date) => (
                  <div key={date}>
                    <div className={`text-center text-xs my-2 ${isDark ? "text-gray-400" : "text-gray-500"}`}>{date}</div>
                    {groupedReplies[date].map((msg, idx) => (
                      <div key={idx} className={`flex ${msg.repliedBy === "admin" ? "justify-end" : "justify-start"}`}>
                        <div className={`inline-block px-4 py-2 text-sm rounded-2xl shadow transition ${msg.repliedBy === "admin" ? "bg-blue-600 text-white rounded-br-none" : chatAltBg + " border rounded-bl-none"}`}>
                          <p className="whitespace-pre-line break-words">{msg.replyText}</p>
                          <div className={`text-[10px] mt-1 ${msg.repliedBy === "admin" ? "text-white text-right" : "text-gray-400 text-left"}`}>
                            {formatChatTimeIST(msg.repliedAt)} {msg.repliedBy === "admin" && "‚úì‚úì"}
                          </div>
                        </div>
                      </div>
                    ))}
                       </div>
                ))}
                <div ref={messagesEndRef} />
              </div>

              {ticket.status === "Resolved" ? (
                  <div className={`text-center font-medium py-4 border-t ${borderColor} ${isDark ? "text-red-400" : "text-red-600"}`}>
                  This ticket is Resolved. No further replies are allowed.
                </div>
              ) : (
                <div className="space-y-3">
                  <label className={`block font-medium ${isDark ? "text-gray-200" : "text-gray-800"}`}>Reply as Admin</label>
                  <textarea
                    value={replyText}
                    onChange={(e) => setReplyText(e.target.value)}
                    onKeyDown={(e) => {
                      if (e.key === "Enter" && !e.shiftKey) {
                        e.preventDefault();
                        handleReply();
                      }
                    }}
                    rows="3"
                    className={`w-full p-3 rounded-lg focus:ring-2 focus:ring-blue-400 resize-none text-sm shadow ${bgColor} ${borderColor}`}
                    placeholder="Type your reply..."
                  />

                  <button
                    onClick={handleReply}
                    className="w-full bg-blue-600 text-white py-2 rounded-lg font-medium hover:bg-blue-700 transition"
                  >
                    Send
                  </button>
                  {messageSent && (
                    <div className="text-green-600 text-sm text-center animate-pulse">
                      ‚úÖ Reply Sent
                    </div>
                  )}
                </div>
              )}
            </>
          )}
        </div>

         <button
          onClick={onClose}
          className={`absolute top-4 right-4 text-2xl transition ${isDark ? "text-gray-400 hover:text-white" : "text-gray-400 hover:text-gray-700"}`}
          aria-label="Close"
        >
          &times;
        </button>
      </div>
    </div>
  );
}